*, ul, li {
  margin: 0;
  padding: 0;
  font-family: Georgia, serif;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
}

.wrapper_main {
  background: #ededed;
  display: flex;
}

@media only screen and (max-width: 1000px) {
  .wrapper_main {
    justify-content: space-evenly;
  }
}

body {
  background: url("../img/bg.jpg") top center no-repeat #c8deeb;
  background-size: 100%;
}

.header-title-description {
  display: flex;
  height: 35px;
}

.header-title-description__title {
  text-transform: uppercase;
  background: #ae0002;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: normal;
  padding: 4px 35px 9px 12px;
  color: #f6f6f6;
  font-size: 18px;
  position: relative;
  border-radius: 2px 0 0 2px;
  white-space: nowrap;
}

@media only screen and (max-width: 500px) {
  .header-title-description__title {
    padding: 10px 20px 9px 12px;
    font-size: 12px;
  }
}

.header-title-description__title:after {
  content: '';
  position: absolute;
  background: #ae0002;
  width: 25px;
  height: 25px;
  top: 5px;
  right: -12px;
  transform: rotate(47deg);
  border-radius: 0 2px 0 0;
}

.header-title-description__description {
  background: #cacaca;
  width: 100%;
  padding-left: 34px;
  font-size: 12px;
  line-height: 35px;
  color: #757575;
  border-radius: 0 2px 2px 0;
}

@media only screen and (max-width: 733px) {
  .header-title-description__description {
    padding-left: 20px;
    line-height: 17px;
  }
}

@media only screen and (max-width: 600px) {
  .header-title-description__description {
    font-size: 0;
  }
}

.hotest {
  padding: 10px 0 0 31px;
  height: 40px;
  border-radius: 2px 2px 0 0;
  background: linear-gradient(to bottom, #fff, #e3e3e3);
}

@media only screen and (max-width: 600px) {
  .hotest {
    display: none;
  }
}

.hotest__logo {
  width: 45px;
  height: 18px;
  background: #85afbd;
  border-radius: 2px;
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  color: #f1f5f7;
  display: inline-block;
  text-align: center;
  line-height: 18px;
}

.hotest__text {
  display: inline-block;
  margin-left: 8px;
  color: #757575;
  font-size: 11px;
  font-style: italic;
  position: relative;
}

.logo-nav {
  display: flex;
  height: 128px;
  background: url("../img/bg_small.jpg");
}

.logo-nav__logo-link {
  padding: 29px 0 0 30px;
}

.menu-small {
  list-style: none;
  padding-top: 24px;
  margin-left: 38px;
}

@media only screen and (max-width: 600px) {
  .menu-small {
    display: none;
  }
}

.menu-small__item {
  text-transform: uppercase;
  margin-right: 10px;
  display: inline-block;
  word-spacing: 4px;
}

.menu-small__link {
  color: #a9a9a9;
  text-decoration: none;
  font-size: 13px;
  font-family: "Barlow Condensed", sans-serif;
}

.menu {
  list-style: none;
  padding-top: 8px;
  margin-left: 24px;
}

@media only screen and (max-width: 600px) {
  .menu {
    display: none;
  }
}

.menu__item {
  text-transform: uppercase;
  margin-right: -7px;
  display: inline-block;
}

.menu__link {
  display: block;
  color: #f6f6f6;
  text-decoration: none;
  font-size: 18px;
  font-family: "Barlow Condensed", sans-serif;
  border-radius: 2px;
  padding: 5px 14px 6px 14px;
  border-right: solid 1px rgba(0, 0, 0, 0);
  border-bottom: solid 1px rgba(0, 0, 0, 0);
  transition: 0.3s;
}

.menu__link:hover {
  background-color: #092f3b;
  border-right: solid 1px rgba(255, 255, 255, 0.15);
  border-bottom: solid 1px rgba(255, 255, 255, 0.15);
  box-shadow: inset 1px 1px 3px 0px rgba(0, 0, 0, 0.25);
}

.toggle {
  display: none;
}

@media only screen and (max-width: 600px) {
  .toggle {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
  }
}

@media only screen and (max-width: 600px) {
  .toggle__item {
    width: 25px;
    height: 3px;
    background: #fff;
    display: block;
    margin-bottom: 5px;
    border-radius: 1px;
  }
}

.main {
  width: 723px;
  margin: 30px 0 0 32px;
}

@media only screen and (max-width: 1000px) {
  .main {
    margin: 30px 0 0 0;
  }
}

@media only screen and (max-width: 925px) {
  .main {
    width: calc(100% - 10px);
  }
}

@media only screen and (max-width: 733px) {
  .main {
    margin: 5px 0 0 0;
  }
}

.main-news {
  display: block;
  height: 300px;
  text-decoration: none;
}

.main-news__img {
  margin-top: 5px;
  background: url("../img/main-news__img.jpg") no-repeat;
  background-size: cover;
  width: 100%;
  height: 256px;
  position: relative;
  transition: 0.3s;
}

.main-news__img img {
  max-width: 100%;
  height: auto;
}

.main-news__img:hover {
  opacity: 0.8;
}

.main-news__text {
  font-size: 12px;
  color: #e2e2e2;
  line-height: 1.5em;
  padding: 19px 23px 21px 17px;
  background: #1e1e1e;
  width: 336px;
  position: absolute;
  bottom: 36px;
  left: 39px;
  text-align: justify;
}

@media only screen and (max-width: 733px) {
  .main-news__text {
    opacity: 0.9;
  }
}

@media only screen and (max-width: 500px) {
  .main-news__text {
    font-size: 11px;
    line-height: 1.5em;
    padding: 25px 5px;
    width: calc(100% - 10px);
    bottom: 5px;
    left: 5px;
  }
}

.news {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 15px;
  margin-top: 11px;
}

@media only screen and (max-width: 733px) {
  .news {
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
    margin-top: 0;
  }
}

.news__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  text-decoration: none;
  border-radius: 2px;
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.15);
  position: relative;
}

.news__item:nth-child(1) .news__img {
  background: url("../img/news__img1.jpg");
}

@media only screen and (max-width: 733px) {
  .news__item:nth-child(1) .news__img {
    background-size: cover;
  }
}

.news__item:nth-child(2) .news__img {
  background: url("../img/news__img2.jpg");
}

@media only screen and (max-width: 733px) {
  .news__item:nth-child(2) .news__img {
    background-size: cover;
  }
}

.news__item:nth-child(3) .news__img {
  background: url("../img/news__img3.jpg");
}

@media only screen and (max-width: 733px) {
  .news__item:nth-child(3) .news__img {
    background-size: cover;
  }
}

.news__item:nth-child(4) .news__img {
  background: url("../img/news__img4.jpg");
}

@media only screen and (max-width: 733px) {
  .news__item:nth-child(4) .news__img {
    background-size: cover;
  }
}

.news__img {
  width: 137px;
  height: 117px;
  transition: 0.3s;
  margin-top: 16px;
}

@media only screen and (max-width: 733px) {
  .news__img {
    width: calc(100% - 10px);
    margin-top: 5px;
    height: 150px;
  }
}

.news__img:hover {
  opacity: 0.8;
}

.news__title {
  font-size: 12px;
  color: #042b38;
  width: 133px;
  text-decoration: underline;
  margin-top: 14px;
  font-weight: 500;
  line-height: 18px;
}

@media only screen and (max-width: 733px) {
  .news__title {
    width: calc(100% - 10px);
    margin-top: 5px;
  }
}

.news__text {
  color: #757575;
  font-size: 11px;
  font-style: italic;
  width: 125px;
  line-height: 18px;
  margin: 7px 0 18px -8px;
}

@media only screen and (max-width: 733px) {
  .news__text {
    width: calc(100% - 10px);
    margin: 5px 0 22px 0;
  }
}

.news__comments {
  position: absolute;
  bottom: 8px;
  right: 12px;
  font-size: 11px;
  font-style: italic;
  color: #989898;
  background: url(../img/speech.png) no-repeat;
  text-align: right;
  padding-left: 23px;
  height: 13px;
  line-height: 8px;
}

.header-title-description:nth-child(3) {
  margin-top: 31px;
}

@media only screen and (max-width: 733px) {
  .header-title-description:nth-child(3) {
    margin: 5px 0 0 0;
  }
}

.long-news {
  margin-top: 44px;
}

@media only screen and (max-width: 733px) {
  .long-news {
    margin: 5px 0 0 0;
  }
}

.long-news__wrapper {
  display: flex;
  margin-top: 26px;
}

@media only screen and (max-width: 733px) {
  .long-news__wrapper {
    margin: 5px 0 0 0;
  }
}

.long-news__img {
  width: 169px;
  height: 129px;
  transition: 0.3s;
  display: inline-block;
  flex: 0 0 auto;
}

@media only screen and (max-width: 600px) {
  .long-news__img {
    width: 100px;
  }
}

.long-news__img_1 {
  background: url("../img/long-news__img1.jpg") no-repeat;
}

@media only screen and (max-width: 600px) {
  .long-news__img_1 {
    background-size: 100%;
  }
}

.long-news__img_2 {
  background: url("../img/long-news__img2.jpg") no-repeat;
}

@media only screen and (max-width: 600px) {
  .long-news__img_2 {
    background-size: 100%;
  }
}

.long-news__text-wrapper {
  margin-left: 27px;
}

@media only screen and (max-width: 733px) {
  .long-news__text-wrapper {
    margin-left: 5px;
  }
}

.long-news__text {
  font-size: 12px;
  color: #757575;
  line-height: 1.5em;
  margin-top: 8px;
}

.long-news__footer {
  margin-top: 16px;
}

@media only screen and (max-width: 733px) {
  .long-news__footer {
    display: flex;
    flex-direction: column;
    margin-top: 5px;
  }
}

.long-news__speech {
  font-size: 12px;
  color: #989898;
  background: url(../img/speech.png) no-repeat;
  text-align: right;
  padding-left: 23px;
  height: 13px;
  line-height: 12px;
  display: inline-block;
  margin-left: -1px;
}

@media only screen and (max-width: 733px) {
  .long-news__speech {
    text-align: left;
    margin-left: 0;
  }
}

.long-news__tags, .long-news__share {
  font-size: 12px;
  color: #989898;
  display: inline-block;
  margin-left: 19px;
}

@media only screen and (max-width: 733px) {
  .long-news__tags, .long-news__share {
    margin: 3px 0 0 0;
  }
}

.long-news__share {
  margin-left: 27px;
  position: relative;
}

.long-news__share a {
  text-decoration: none;
  position: absolute;
}

.long-news__share a:nth-child(1) {
  top: -1px;
  left: 47px;
}

.long-news__share a:nth-child(2) {
  top: -1px;
  left: 68px;
}

.long-news__tags-item {
  font-size: 12px;
  font-style: italic;
  color: #2b5e5e;
}

.long-news__tags-item:nth-child(1) {
  padding-left: 7px;
}

.aside {
  display: inline-block;
  width: 187px;
  margin: 30px 0 0 27px;
}

@media only screen and (max-width: 1000px) {
  .aside {
    margin: 30px 0 0 0;
  }
}

@media only screen and (max-width: 925px) {
  .aside {
    display: none;
  }
}

.aside__header {
  text-transform: uppercase;
  background: #ae0002;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: normal;
  padding: 4px 0 9px 13px;
  color: #f6f6f6;
  font-size: 18px;
  border-radius: 2px;
  width: 187px;
}

.aside__link {
  display: block;
  text-decoration: none;
}

.aside__img {
  width: 187px;
  height: 172px;
  border-radius: 2px;
  position: relative;
  transition: 0.3s;
}

.aside__img:hover {
  opacity: 0.8;
}

.aside__img_1 {
  background: url("../img/aside__img1.jpg") no-repeat;
  margin-top: 5px;
}

.aside__img_2 {
  background: url("../img/aside__img2.jpg") no-repeat;
  margin-top: 22px;
}

.aside__img_3 {
  background: url("../img/aside__img3.jpg") no-repeat;
  margin-top: 22px;
}

.aside__icon {
  position: absolute;
  left: 8px;
  bottom: 9px;
  width: 35px;
  height: 35px;
  background: url(../img/triangle.png) no-repeat;
}

.aside__title {
  font-family: Georgia,serif;
  font-size: 12px;
  color: #2e2e2e;
  text-decoration: underline;
  color: #042b38;
  margin: 16px 0 0 3px;
  font-weight: 500;
  line-height: 18px;
  width: 184px;
}

.aside__text {
  color: #757575;
  font-size: 11px;
  font-style: italic;
  line-height: 1.5em;
  margin: 7px 0 0 3px;
  position: relative;
  line-height: 18px;
  width: 184px;
}

.aside__comments {
  position: absolute;
  left: 57px;
  bottom: 2px;
  font-size: 11px;
  font-style: italic;
  color: #989898;
  background: url(../img/speech.png) no-repeat;
  text-align: right;
  height: 13px;
  line-height: 8px;
  padding-left: 23px;
}
